import {
  Component
} from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams
} from 'ionic-angular';
import {
  CompanyProfilePage
} from '../company-profile/company-profile';

import {
  AngularFireDatabase
} from 'angularfire2/database';
import {
  Observable
} from 'rxjs/Observable';

@IonicPage()
@Component({
  selector: 'page-search-results',
  templateUrl: 'search-results.html',
})
export class SearchResultsPage {
  items: Observable < any[] > ;
  letterID: string;
  startAtKey: string = "hello"
  pageLength = 10;
  nextPageStartKey: string = "hello"
  pageNum: any = '0'
  hideNextButton = true

  constructor(public navCtrl: NavController, public navParams: NavParams, afDB: AngularFireDatabase) {


    this.letterID = navParams.get('letter');
    this.startAtKey = navParams.get('startAtKey');
    this.pageNum = navParams.get('pageNum'); //get page number

    if (this.pageNum === 1) {
      this.items = afDB.list('bizDirectoryByName/' + this.letterID, ref => ref.orderByKey().limitToFirst(this.pageLength)).valueChanges();


    } else {
      this.items = afDB.list('bizDirectoryByName/' + this.letterID, ref => ref.orderByKey().startAt(this.startAtKey).limitToFirst(this.pageLength)).valueChanges();

    }

    // subscribe to changes
    this.items.subscribe(queriedItems => {
      
      console.log("test: " + queriedItems.length)

      if (queriedItems.length < this.pageLength) {
        // variable is undefined
        this.hideNextButton = false
      //  console.log('not there')
      } else {
        var nextKey = queriedItems[this.pageLength - 1].name
        this.nextPageStartKey = nextKey
       // console.log('its there')
      }
    });

  }

  //next page
  nextpage() {
    this.navCtrl.push(SearchResultsPage, {
      'letter': this.letterID,
      'startAtKey': this.nextPageStartKey,
      'pageNum': this.pageNum + 1
    });
  }


  viewCompany(letter, name) {
    this.navCtrl.push(CompanyProfilePage, {
      'letter': letter,
      'companyName': name
    });
  }

}



