import { BrowserModule } from "@angular/platform-browser";
import { ErrorHandler, NgModule } from "@angular/core";
import { LocationStrategy, PathLocationStrategy, APP_BASE_HREF } from '@angular/common';
import { IonicApp, IonicErrorHandler, IonicModule, DeepLinkConfig } from "ionic-angular";
import { SplashScreen } from "@ionic-native/splash-screen";
import { StatusBar } from "@ionic-native/status-bar";

import { MyApp } from "./app.component";
import { HomePage } from "../pages/home/home";
import { CompanyProfilePageModule } from "../pages/company-profile/company-profile.module";
import { CompanyProfilePage } from "../pages/company-profile/company-profile";
import { SearchResultsPageModule } from "../pages/search-results/search-results.module";
import { SearchResultsPage } from "../pages/search-results/search-results";

import { Deeplinks } from "@ionic-native/deeplinks";
import {
  AngularFireModule
} from 'angularfire2';
import {
  AngularFireDatabaseModule,
  AngularFireDatabase
} from 'angularfire2/database';
import {
  AngularFireAuthModule
} from 'angularfire2/auth';


export const firebaseConfig = {
  apiKey: "AIzaSyCmtiiTHZXKpEL3Hb1Ofe0ClE9cunzHzR8",
  authDomain: "qeyword-17e55.firebaseapp.com",
  databaseURL: "https://qeyword-17e55.firebaseio.com",
  projectId: "qeyword-17e55",
  storageBucket: "qeyword-17e55.appspot.com",
  messagingSenderId: "782631173502"
};


export const deepLinkConfig: DeepLinkConfig = {
  links: [
    { component: HomePage, name: 'home', segment: '' },
    { component: CompanyProfilePage, name: 'company', segment: 'companies/:letter/:companyName', defaultHistory: [HomePage] },
    { component: SearchResultsPage, name: 'search', segment: 'search/:letter/:pageNum/:startAtKey', defaultHistory: [HomePage] }
  ]
};


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    SearchResultsPage,
    CompanyProfilePage,
  ],
  imports: [
    BrowserModule,
    SearchResultsPageModule,
    IonicModule.forRoot(MyApp, { locationStrategy: 'path' }, deepLinkConfig),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    SearchResultsPage,
    CompanyProfilePage


  ],
  providers: [
    StatusBar,
    SplashScreen,
    Deeplinks,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    // { provide: LocationStrategy, useClass: PathLocationStrategy },
    { provide: APP_BASE_HREF, useValue: '/directory' }
  ]
})
export class AppModule {
}



